import React, { ReactElement, useCallback, useMemo } from 'react';
import { server, POST } from '../utils/requests';
import url from 'url';
import styles from '../css/tournament.module.css';
import { TournamentStatus, TournamentSettings } from '../interfaces';
import { EditableField } from '../components/';
import { MdExitToApp, MdStop, MdPause } from 'react-icons/md';
import { IoMdPlay, IoMdGrid } from 'react-icons/io';
import { TiFlowSwitch } from 'react-icons/ti';
import { FaAngleLeft, FaAngleRight } from 'react-icons/fa';

interface InfoProps {
	userRound: number
	setRound: (round: number) => void

	status: TournamentStatus
	settings: TournamentSettings
	updateTournamentStatus: () => void
	round: number
	totalRounds: number | 'Infinity'
	closeTournament: () => void
	toggleCrossTable: () => void

	lastRoundOverride: boolean
	setLastRound: (override: boolean) => void
	triggerError: (message: string) => void
	switchPairings: (set?: boolean) => void
}

export default function InfoProps(props: InfoProps): ReactElement {

	let lastRound = false;
	if (typeof props.lastRoundOverride !== 'undefined') lastRound = props.lastRoundOverride;
	else lastRound = props.settings.totalRounds === props.status.round;

	let uri = url.resolve(server, ['tournament', props.status.id, 'setName'].join('/'));

	const round = useMemo(() => props.userRound || props.status.round, [props.userRound, props.status.round]);

	const decrementRound = useCallback(() => {
		if (round > 1) {
			props.setRound(round - 1);
			props.switchPairings(false);
		}
	}, [round, props.setRound, props.switchPairings]);

	const incrementRound = useCallback(() => {
		if (round < props.status.round) {
			props.setRound(round + 1);
			props.switchPairings(false);
		}
	}, [round, props.setRound, props.switchPairings]);

	return (
		<div className={styles.titleBar} style={{
			gridColumn: '2 /span 1'
		}}>
			<div className={styles.row}>
				<div className={['topBanner', styles.header].join(' ')}>
					<EditableField
						content={props.status.name}
						url={uri}
						inputType='text'
						handleSubmit={(k: string, v: any) => POST({
							url: uri,
							data: {
								[k]: v
							}
						})}
						additionalStyles={styles}
						additionalInputProps={{}}
						onSubmitSuccess={(e) => {
							props.updateTournamentStatus();
							return e;
						}}
						defaultContent='Unnamed Swiss Tournament'
						show={{
							submit: true
						}}
					/>
				</div>
			</div>
			<div className={[styles.row, styles.statusBar].join(' ')}>
				<div id={styles.previous} className={[round > 1 ? 'button' : styles.fixed].join(' ')} onClick={decrementRound}>
					<FaAngleLeft />
				</div>
				<div className={styles.fixed}>
                    Round: {!round ? 'Not yet initialised' : round + ' / ' + (props.totalRounds === 'Infinity' ? '∞' : props.totalRounds)}
				</div>
				<div id={styles.next} className={[round < props.status.round ? 'button' : styles.fixed].join(' ')} onClick={incrementRound}>
					<FaAngleRight />
				</div>
			</div>
			<div className={styles.row}>
				<div className={['button', styles.button].join(' ')} onClick={props.closeTournament}>
					<MdExitToApp style={{color: 'indianred'}} /> Exit to menu
				</div>
				<div className={['button', styles.button].join(' ')} onClick={() => {
					if (props.status.round >= props.settings.totalRounds) props.triggerError('Increase the number of rounds in Settings to continue!');
					props.setLastRound(!lastRound);
				}}>
					{lastRound ? 
						<><IoMdPlay style={{color: 'yellow'}} />{' '}Resume tournament</> :
						<><MdPause style={{color: 'yellow'}} />{' '}Pause tournament</>
					}
				</div>
			</div>
			<div className={styles.row}>
				<div className={['button', styles.button].join(' ')} onClick={props.toggleCrossTable}>
                	<><IoMdGrid style={{color: 'green'}} />{' '}Crosstable</>
				</div>
				<div className={[round === props.status.round ? styles.active : styles.inactive, 'button', styles.button].join(' ')} onClick={round === props.status.round ? () => props.switchPairings() : () => {}}>
                	{round === props.status.round ? <><TiFlowSwitch style={{color: '#385898'}} />{' '}Switch Pairings</> : '\u200b'}
				</div>
			</div>
		</div>
	);

}